import React from 'react'
import styled, { css } from 'styled-components';

const ProgressBarContainer = styled.div`
  display: flex;

  ${({ vertical }) => vertical && css`
    width: 12px;
    margin-left: 15px;
    height: ${({ dimension }) => dimension || 100}%;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: space-between;

    > label {
      margin-top: 7px;
    }
  `}

  ${({ vertical }) => !vertical && css`
    height: 12px;
    margin-top: 15px;
    width: ${({ dimension }) => dimension || 100}%;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;

    > label {
      margin-left: 7px;
    }
  `}
`

const ProgressBarStyled = styled.div`
  height: 100%;
  width: 100%;
  border: 1px solid ${({ color }) => color };

  &::after {
    position: relative;
    display: flex;
    content: '';
    background: ${({ color }) => color };

    ${({ vertical }) => vertical && css`
      bottom: 0;
      height: ${({ progress }) => progress || 0}%;
      width: 100%;
    `}

    ${({ vertical }) => !vertical && css`
      left: 0;
      width: ${({ progress }) => progress || 0}%;
      height: 100%;
    `}
  }
`

const Label = styled.span`
  color: ${({ color }) => color};
  font-size: 0.8rem;
`

const ProgressBar = ({ progress, color, dimension, vertical = false }) => {
  return (
    <ProgressBarContainer dimension={dimension} vertical={vertical}>
      <ProgressBarStyled progress={progress} color={color} vertical={vertical}/>
      <Label color={color}>{progress}%</Label>
    </ProgressBarContainer>
  )
}

export default ProgressBar
