import React from 'react'
import styled from 'styled-components';
import { Router, Route, Switch, Link } from 'react-router-dom';
import { AnimatedSwitch } from 'react-router-transition';
import Logo from '../../assets/logo.png';

import history from "../history";
import GetStarted from '../Views/GetStarted/GetStarted';
import Enterprises from '../Views/Enterprises/Enterprises';
import EnterpriseProgress from '../Views/EnterpriseProgress/EnterpriseProgress';
import Economy from '../Views/Economy/Economy';
import Home from '../Views/Home/Home';
import Inbox from '../Views/Inbox/Inbox';

const SidemenuStyled = styled.div`
  width: 80%;
  height: 100%;
  background: rgba(30,30,30,1);
  position: absolute;
  transform: ${({ open }) => open ? 'translate3d(0,0,0)' : 'translate3d(-100%,0,0)'};
  transition: all 0.7s ease;
`

const BackMenu = styled.button`
  all: unset;
  margin-right: 20px;
  cursor: pointer;
  position: absolute;
  left: 10px;
  top: 10px;

  .fas {
    color: var(--primary-gold);
    font-size: 20px;
  }
`

const Menu = styled.ul`
  padding: 0;
  margin: 40px 0 0;
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: flex-start;
`

const MenuItem = styled.li`
  width: 100%;
  height: 52px;
  border-bottom: 1px solid #220202;
  border-top: 1px solid #5e2727;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  padding-left: 10px;

  a {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    text-decoration: none;
    padding-left: 10px;
    color: var(--primary-gold);
  }
`

const Icon = styled.div`
  width: 25px;
  height: 25px;
  margin-right: 7px;

  .fas {
    color: var(--primary-gold);
    font-size: 24px;
  }
`

const LogoContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;

  > img {
    width: 40%;
  }
`

const Sidemenu = ({ handleOnClick, open }) => (
  <Router history={history}>
    <SidemenuStyled open={open} >
      <BackMenu onClick={handleOnClick}>
        <i className="fas fa-arrow-left" />
      </BackMenu>
      <LogoContainer>
        <img src={Logo} />
      </LogoContainer>
      <Menu>
        <MenuItem>
          <Icon><i className="fas fa-home" /></Icon>
          <Link to="/home" onClick={handleOnClick}>Home</Link>
        </MenuItem>
        <MenuItem>
          <Icon><i className="fas fa-building" /></Icon>
          <Link to="/meus-empreendimentos" onClick={handleOnClick}>Meus Empreendimentos</Link>
        </MenuItem>
        <MenuItem>
          <Icon><i className="fas fa-tractor" /></Icon>
          <Link to="/andamento-da-obra" onClick={handleOnClick}>Andamento da Obra</Link>
        </MenuItem>
        <MenuItem>
          <Icon><i className="fas fa-chart-line" /></Icon>
          <Link to="/indices-economicos" onClick={handleOnClick}>Índices Econômicos</Link>
        </MenuItem>
        <MenuItem>
          <Icon><i className="fas fa-inbox" /></Icon>
          <Link to="/caixa-de-entrada" onClick={handleOnClick}>Caixa de Entrada</Link>
        </MenuItem>
      </Menu>
    </SidemenuStyled>
    <Switch>
      <Route path="/" exact component={GetStarted} />
      <Route path="/home" component={Home} />
      <Route path="/meus-empreendimentos" component={Enterprises} />
      <Route path="/andamento-da-obra" component={EnterpriseProgress} />
      <Route path="/indices-economicos" component={Economy} />
      <Route path="/caixa-de-entrada" component={Inbox} />
    </Switch>
  </Router>
)

export default Sidemenu
